import React from 'react'
import { Outlet, Link } from 'react-router-dom';

import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import MenuItem from '@mui/material/MenuItem';
import Paper from '@mui/material/Paper';
export default function LayoutApp() {
    return (<Paper sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        width: '100%',
        height: '98vh'
    }}>
        <AppBar position="sticky" >
            <Toolbar variant="dense">
            <MenuItem component={Link} to="/" key="solution">
                    <Typography textAlign="center">Resolver Problema</Typography>
                </MenuItem>
                <MenuItem component={Link} to="/get-products" key="products">
                    <Typography textAlign="center">Productos</Typography>
                </MenuItem>
                <MenuItem component={Link} to="/get-parameters" key="parameters">
                    <Typography textAlign="center">Parámetros</Typography>
                </MenuItem>
            </Toolbar>
        </AppBar>


        <Outlet />
    </Paper>
    );
}