
import React, { useEffect, useState } from 'react'
import { useNavigate, Link } from "react-router-dom";
import { Card, CardHeader, CardContent, Button, CardActions } from '@mui/material'
import { Box } from '@mui/material';
import { DataGrid, GridActionsCellItem } from '@mui/x-data-grid';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
  borderRadius: 5, 
};

const ProductList = () => {
  const [products, setProducts] = useState([]);
  const [open, setOpen] = React.useState(false);
  const handleClose = () => setOpen(false);
  const [productDelete, setProductDelete] = useState({});

  const navigate = useNavigate();
  const updateList = ()=>{
    let routeAPI
    routeAPI = `http://${process.env.REACT_APP_HOST_ADDRESS}:${process.env.REACT_APP_PORT}/api/get-products`

    fetch(routeAPI, {
      method: 'GET'
    })
      .then(response => {
        console.log(response)
        if (!response.ok) {
          return response.json()
            .then(error => Promise.reject(`${response.status} ${response.statusText}: ${error.message}`));
        }
        return response.json();
      })
      .then((data) => {
        console.log(data)
        setProducts(data)
      })
      .catch(error => {
        console.log(error)
        alert(error);
      });
      
    }
  
  

    useEffect(() => {
      updateList()
    }, [])

    useEffect(() => {
      console.log(products)
    }, [products])

  const editProduct = (id)=>{
    navigate(`/put-products/${id}`)
  }

  const openDeleteModal = (row) => {
    setProductDelete(row)
    setOpen(true)

  }

  const deleteProduct = ()=>{
    handleClose()
    let routeAPI = `http://${process.env.REACT_APP_HOST_ADDRESS}:${process.env.REACT_APP_PORT}/api/delete-product/${productDelete.id}`
    fetch(routeAPI, {
      method: 'DELETE'
    })
      .then(response => {
        console.log(response)
        if (!response.ok) {
          return response.json()
            .then(error => Promise.reject(`${response.status} ${response.statusText}: ${error.message}`));
        }
        return response.json();
      })
      .then((data) => {
        handleClose()
        alert(data.Message)
        updateList()
      })
      .catch(error => {
        console.log(error)
        alert(error);
      });
    }

  const columns = [
    {
      field: 'product_name',
      headerName: 'Nombre de producto',
      flex: 1,
      maxWidth: "25%",
      headerAlign: 'center',
      align: 'center'
    },
    {
      field: 'weight',
      headerName: 'Peso del producto (kg)',
      flex: 1,
      maxWidth: "25%",
      headerAlign: 'center',
      align: 'center'
    },
    {
      field: 'buy_price',
      headerName: 'Precio de Compra',
      flex: 1,
      maxWidth: "25%",
      headerAlign: 'center',
      align: 'center'
    },
    {
      field: 'sale_price',
      headerName: 'Precio de Venta',
      flex: 1,
      maxWidth: "25%",
      headerAlign: 'center',
      align: 'center'
    },
    {
      field: 'actions',
      type: 'actions',
      width: 80,
      getActions: (row) => [
        <GridActionsCellItem
          icon={<DeleteIcon />}
          label="Delete"
          onClick={()=>{openDeleteModal(row.row)}}
        />,
        <GridActionsCellItem
          icon={<EditIcon />}
          label="Toggle Admin"
          onClick={()=>{editProduct(row.id)}}
        />,
      ],
    },
  ]

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        width: '90%',
        height: '90%'
      }}
    >
      <Card
        sx={{
          flexDirection: 'row',
          alignItems: 'center',
          justifyContent: 'center',
          width: '90%',
          height: '100%',
          overflow: 'auto'
        }} >
        <CardHeader title="Lista de Productos Disponibles"
          sx={{
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
          }} />
        <CardContent sx={{ height: '80%', width: "80%", margin: "auto" }}>
          <DataGrid
            rows={products}
            columns={columns}
            getRowId={(row) => row.id}
            getRowHeight={() => 'auto'}
            initialState={{
              pagination: {
                paginationModel: {
                  pageSize: 10,
                },
              },
            }
          }
            pageSizeOptions={[5]}
          />
        </CardContent>
        <CardActions>
          <Button component={Link} to="/post-product" size="small"
            sx={{
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              justifyContent: 'center',
              width: '100%',
              height: '100%'
            }}>
              Añadir Producto
            </Button>
          </CardActions>
      </Card>

      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-title"
        aria-describedby="modal-description"
      >
        <Box sx={style}>
          <Typography id="modal-title" variant="h6" component="h2">
            Confirmación de eliminación de datos
          </Typography>
          <Typography id="modal-description" sx={{ mt: 2 }}>
            ¿Está seguro de eliminar "{productDelete.product_name}"<br/>
          </Typography>
          <Button onClick={handleClose} variant="contained" sx={{ m: 2 }}> Cancelar</Button>
          <Button onClick={deleteProduct} color="error" variant="contained" sx={{ m: 2 }}> Eliminar</Button>
        </Box>
      </Modal>


    </Box>
  )
}

export default ProductList