import React from 'react'
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import ParametersIA from './MainComponents/ParametersIA';
import PutParameters from './MainComponents/PutParameters';
import ProductList from './MainComponents/ProductList';
import PutProducts from './MainComponents/PutProducts';
import PostProducts from './MainComponents/PostProducts';
import SolutionGA from './MainComponents/SolutionGA';

import LayoutApp from './MainComponents/LayoutApp';
function AIRoute() {
  return (
    <BrowserRouter>
        <Routes>
        <Route path="/" element={<LayoutApp/>}>
        <Route index element={<SolutionGA/>}></Route>
        <Route path="/get-parameters" element={<ParametersIA/>}></Route>
        <Route path="/put-parameters" element={<PutParameters/>}></Route>
        <Route path="/get-products" element={<ProductList/>}></Route>
        <Route path="/put-products/:id" element={<PutProducts/>}></Route>
        <Route path="/post-product" element={<PostProducts/>}></Route>
        </Route>
        
        </Routes>
    </BrowserRouter>
  );
}

export default AIRoute;
