
import Grid from '@mui/material/Grid';
import React, { useEffect, useState } from 'react';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import FormGroup from '@mui/material/FormGroup';
import Card from '@mui/material/Card';
import { useParams } from 'react-router-dom';
import { CardContent, CardHeader } from '@mui/material';
import { useNavigate, useLocation } from 'react-router-dom';

function PostProducts(props) {
  let id = useParams().id
  const navigate = useNavigate();
  

  const [product, setProduct] = useState({});
  const [errorName, setErrorName] = useState('')
  const [errorWeight, setErrorWeight] = useState('')
  const [errorBuyPrice, setErrorBuyPrice] = useState('')
  const [errorSalePrice, setErrorSalePrice] = useState('')
  const [disabled, setDisabled] = useState(true)
  
  useEffect(() => {
    if (product.product_name !== '') {
      setErrorName('')
    }
    else {
        setErrorName('El nombre no puede ser vacío')
    }
    
    if (product.weight > 0) {
      setErrorWeight('')
    }
    else {
      setErrorWeight('El peso del producto (kg) debe ser mayor a 0')
    }
    if (product.sale_price > 0) {
        setErrorSalePrice('')
      }
      else {
        setErrorSalePrice('El precio de compra debe ser mayor a 0')
      }if (product.buy_price > 0) {
        setErrorBuyPrice('')
      }
      else {
        setErrorBuyPrice('El precio de venta debe ser mayor a 0')
      }
    if(product.product_name === '' || product.weight < 0 || product.sale_price < 0 || product.buy_price < 0 ){
      console.log("Entra a disable")
      setDisabled(true)
    }
    else{
      setDisabled(false)
    }
  }, [product])

  const handleSubmit = (e) => {
    let route = `http://${process.env.REACT_APP_HOST_ADDRESS}:${process.env.REACT_APP_PORT}/api/post-products`
    e.preventDefault()
    let requestData = JSON.stringify(product)
 
      fetch(route, {
        method: 'POST',
        body: requestData,
        headers: {
          'Content-Type': 'application/json'
        }
      })
        .then(response => {
          if (!response.ok) {
            alert("Something went wrong")
          }
          else {
            navigate(`/get-products`);
          }
        })
        .catch(error => {
          alert(error)
        });
    

  }
  return (
    <Card>
      <CardHeader title="Insertar Productos"></CardHeader>
      <CardContent m={2} >
        <form onSubmit={handleSubmit}>
          <FormGroup sx={{ padding: 2, borderRadius: 2, border: '1px solid', borderColor: 'primary.main' }}>
            <Grid container spacing={2} p={2}>
              <Grid item xs={12}>
                <TextField
                  name='product_name'
                  type="text"
                  required
                  fullWidth
                  id="outlined-required"
                  label="Nombre del producto"
                  InputLabelProps={{ shrink: true }} 
                  value={product.product_name}
                  onChange={e => setProduct({
                    ...product,
                    product_name: e.target.value
                  })}
                  error={!!errorName}
                  helperText={errorName}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  name='weight'
                  type="number"
                  InputProps={{ inputProps: { min: 0, step: 0.1} }}
                  required
                  fullWidth
                  id="outlined-required"
                  label="Peso (kg) del producto"
                  InputLabelProps={{ shrink: true }} 
                  value={product.weight}
                  onChange={e => setProduct({
                    ...product,
                    weight: e.target.value
                  })}
                  error={!!errorWeight}
                  helperText={errorWeight}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  name='buy_price'
                  type="number"
                  InputProps={{ inputProps: { min: 0, step: 0.01} }}
                  required
                  fullWidth
                  id="outlined-required"
                  label="Precio de Compra"
                  InputLabelProps={{ shrink: true }} 
                  value={product.buy_price}
                  onChange={e => setProduct({
                    ...product,
                    buy_price: e.target.value
                  })}
                  error={!!errorBuyPrice}
                  helperText={errorBuyPrice}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  name='sale_price'
                  type="number"
                  InputProps={{ inputProps: { min: 0, step: 0.01} }}
                  required
                  fullWidth
                  id="outlined-required"
                  label="Precio de Venta"
                  InputLabelProps={{ shrink: true }} 
                  value={product.sale_price}
                  onChange={e => setProduct({
                    ...product,
                    sale_price: e.target.value
                  })}
                  error={!!errorSalePrice}
                  helperText={errorSalePrice}
                />
              </Grid>
              <Grid item xs={12} >
              <Button type="submit" variant="outlined" disabled={disabled}>Enviar </Button>
              </Grid>
            </Grid>
          </FormGroup>
        </form>
      </CardContent>

    </Card>

  );


}

export default PostProducts;