
import React, { useEffect, useState } from 'react'
import { Link } from "react-router-dom";
import { Card, CardHeader, CardContent, CardActions, Button } from '@mui/material'
import { Box } from '@mui/material';
import { DataGrid } from '@mui/x-data-grid';

const ParametersIA = () => {
  const [parametersAI, setParametersAI] = useState([]);

  const columns = [
    {
      field: 'n_pop',
      headerName: 'No. de Población',
      flex: 1,
      headerAlign: 'center',
      align: 'center'
    },
    {
      field: 'n_generations',
      headerName: 'No. de Generaciones',
      flex: 1,
      headerAlign: 'center',
      align: 'center'
    },
    {
      field: 'p_selection',
      headerName: '% de selección',
      flex: 1,
      headerAlign: 'center',
      align: 'center'
    },
    {
      field: 'p_mutate',
      headerName: '% de mutación',
      flex: 1,
      headerAlign: 'center',
      align: 'center'
    },
    {
      field: 'min_value',
      headerName: 'Valor Mínimo',
      flex: 1,
      headerAlign: 'center',
      align: 'center'
    },
    {
      field: 'max_value',
      headerName: 'Valor Máximo',
      flex: 1,
      headerAlign: 'center',
      align: 'center'
    },
    {
      field: 'max_weight',
      headerName: 'Peso Máximo',
      flex: 1,
      headerAlign: 'center',
      align: 'center'
    },
    {
      field: 'budget',
      headerName: 'Presupuesto',
      flex: 1,
      headerAlign: 'center',
      align: 'center'
    },

  ]

  useEffect(() => {
    let routeAPI
    routeAPI = `http://${process.env.REACT_APP_HOST_ADDRESS}:${process.env.REACT_APP_PORT}/api/get-variables`

    fetch(routeAPI, {
      method: 'GET'
    })
      .then(response => {
        console.log(response)
        if (!response.ok) {
          return response.json()
            .then(error => Promise.reject(`${response.status} ${response.statusText}: ${error.message}`));
        }
        return response.json();
      })
      .then((data) => {
        console.log(data)
        setParametersAI([data])
      })
      .catch(error => {
        console.log(error)
        alert(error);
      });
  }, [])
  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        width: '90%',
        height: '90%'
      }}
    >
      <Card
        sx={{
          flexDirection: 'row',
          alignItems: 'center',
          justifyContent: 'center',
          width: '90%',
          height: '90%',
        }} >
        <CardHeader title="Datos de Parámetros para Algoritmo Genético"
          sx={{
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
          }} />
        <CardContent sx={{ height: '70%', width: "70%", margin: "auto" }}>
          <DataGrid
            rows={parametersAI}
            columns={columns}
            getRowHeight={() => 'auto'}
            initialState={{
              pagination: {
                paginationModel: {
                  pageSize: 10,
                },
              },
            }}
            pageSizeOptions={[10]}
            pagination
          />
        </CardContent>
        <CardActions>
          <Button component={Link} to="/put-parameters" size="small"
            sx={{
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              justifyContent: 'center',
              width: '100%',
              height: '100%'
            }}>
              Editar
            </Button>
          </CardActions>
      </Card>



    </Box>
  )
}

export default ParametersIA