
import React, { useState } from 'react'
import { Card, CardHeader, CardContent, Button, CardActions } from '@mui/material'
import { Box } from '@mui/material';
import { DataGrid } from '@mui/x-data-grid';
import CircularProgress from '@mui/material/CircularProgress';
import { green } from '@mui/material/colors';


const SolutionGA = () => {
  const [combination, setCombination] = useState([]);
  const [score, setScore] = useState(0);
  const [disable, setDisable] = useState(false);

  const getSolution = () => {
    setDisable(true)
    let routeAPI
    routeAPI = `http://${process.env.REACT_APP_HOST_ADDRESS}:${process.env.REACT_APP_PORT}/api/solve`

    fetch(routeAPI, {
      method: 'GET'
    })
      .then(response => {
        console.log(response)
        if (!response.ok) {
          return response.json()
            .then(error => Promise.reject(`${response.status} ${response.statusText}: ${error.message}`));
        }
        return response.json();
      })
      .then((data) => {
        setScore(data.score)
        setCombination(data.combination)
        console.log(combination)
        setDisable(false)
      })
      .catch(error => {
        console.log(error)
        alert(error);
      });

  }



  const columns = [
    {
      field: 'product_name',
      headerName: 'Nombre de producto',
      flex: 1,
      maxWidth: "50%",
      headerAlign: 'center',
      align: 'center'
    },
    {
      field: 'quantity',
      headerName: 'Cantidad',
      flex: 1,
      maxWidth: "50%",
      headerAlign: 'center',
      align: 'center'
    },
  ]

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        width: '90%',
        height: '90%'
      }}
    >
      <Card
        sx={{
          flexDirection: 'row',
          alignItems: 'center',
          justifyContent: 'center',
          width: '90%',
          height: '100%',
          overflow: 'auto'
        }} >
        <CardHeader title={`Solución con Algoritmo Genético: ${score}`}
          sx={{
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
          }} />
        <CardContent sx={{ height: '80%', width: "80%", margin: "auto" }}>
          <DataGrid
            rows={combination}
            columns={columns}
            getRowId={(row) => row.id}
            getRowHeight={() => 'auto'}
            initialState={{
              pagination: {
                paginationModel: {
                  pageSize: 10,
                },
              },
            }
            }
            pageSizeOptions={[5]}
          />
        </CardContent>
        <CardActions>
          {disable
            ?
            <Box sx={{ m: 1, position: 'relative' }}>
              <Button
                variant="contained"
                disabled={disable}
              >
              </Button>
              {disable && (
                <CircularProgress
                  size={24}
                  sx={{
                    color: green[500],
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    marginTop: '-12px',
                    marginLeft: '-12px',
                  }}
                />
              )}
            </Box>
            :
            <Button onClick={getSolution} size="small"
              sx={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                justifyContent: 'center',
                width: '100%',
                height: '100%'
              }}>
              Generar Solución
            </Button>
          }




        </CardActions>
      </Card>
    </Box>
  )
}

export default SolutionGA