
import Grid from '@mui/material/Grid';
import React, { useEffect, useState } from 'react';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import FormGroup from '@mui/material/FormGroup';
import Card from '@mui/material/Card';
import { Box } from '@mui/material';
import { useParams } from 'react-router-dom';
import { CardContent, CardHeader } from '@mui/material';
import { useNavigate, useLocation } from 'react-router-dom';

function PutParameters(props) {
  const navigate = useNavigate();
  const location = useLocation();
  

  const [parametersAI, setParametersAI] = useState({});
  const [errorPob, setErrorPob] = useState('')
  const [errorGen, setErrorGen] = useState('')
  const [errorSelect, setErrorSelect] = useState('')
  const [errorMutate, setErrorMutate] = useState('')
  const [errorMax, setErrorMax] = useState('')
  const [errorMin, setErrorMin] = useState('')
  const [errorMaxWeight, setErrorMaxWeight] = useState('')
  const [errorBudget, setErrorBudget] = useState('')
  const [disabled, setDisabled] = useState(true)
  useEffect(() => {
    let routeAPI
    routeAPI = `http://${process.env.REACT_APP_HOST_ADDRESS}:${process.env.REACT_APP_PORT}/api/get-variables`

    fetch(routeAPI, {
      method: 'GET'
    })
      .then(response => {
        console.log(response)
        if (!response.ok) {
          return response.json()
            .then(error => Promise.reject(`${response.status} ${response.statusText}: ${error.message}`));
        }
        return response.json();
      })
      .then((data) => {
        setParametersAI(data)
      })
      .catch(error => {
        console.log(error)
        alert(error);
      });
}, [])

  useEffect(() => {
    if (parametersAI.n_pop > 0) {
      setErrorPob('')
    }
    else {
      setErrorPob('La población debe ser mayor a 0')
    }
    
    if (parametersAI.n_generations > 0) {
      setErrorGen('')
    }
    else {
      setErrorGen('El número de generaciones debe ser mayor a 0')
    }
    if (parametersAI.p_selection > 0 && parametersAI.p_selection < 1.0) {
      setErrorSelect('')
    }
    else {
      setErrorSelect('El % de selección debe ser mayor a 0 y menor a 1')
    }
    
    if (parametersAI.p_mutate > 0 && parametersAI.p_mutate < 1.0) {
      setErrorMutate('')
    }
    else {
      setErrorMutate('El % de mutación debe ser mayor a 0 y menor a 1')
    }
    if (parametersAI.max_value > parametersAI.min_value) {
      setErrorMax('')
    }
    else {
      setErrorMax('El máximo de productos debe ser mayor que el mínimo')
    }
    if (parametersAI.min_value >= 0) {
      setErrorMin('')
    }
    else {
      setErrorMin('El mínimo de productos debe ser mayor o igual a 0')
    }
    
    if (parametersAI.max_weight >= 0) {
      setErrorMaxWeight('')
    }
    else {
      setErrorMaxWeight('El máximo de peso no debe ser menor a 0')
    }
    
    if (parametersAI.budget >= 0) {
      setErrorBudget('')
    }
    else {
      setErrorBudget('El presupuesto no debe de ser menor a 0')
    }

    if(parametersAI.n_pop < 0 || parametersAI.n_generations < 0 || (parametersAI.p_selection < 0 || parametersAI.p_selection > 1.0) ||(parametersAI.p_mutate < 0 || parametersAI.p_mutate > 1.0) || parametersAI.max_value < parametersAI.min_value|| parametersAI.min_value < 0 || parametersAI.max_weight < 0 || parametersAI.budget < 0){
      console.log("Entra a disable")
      setDisabled(true)
    }
    else{
      setDisabled(false)
    }
  }, [parametersAI])

  const handleSubmit = (e) => {
    let route = `http://${process.env.REACT_APP_HOST_ADDRESS}:${process.env.REACT_APP_PORT}/api/put-variables`
    e.preventDefault()
    let requestData = JSON.stringify(parametersAI)
 
      fetch(route, {
        method: 'PUT',
        body: requestData,
        headers: {
          'Content-Type': 'application/json'
        }
      })
        .then(response => {
          if (!response.ok) {
            alert("Something went wrong")
          }
          else {
            navigate(`/`);
          }
        })
        .catch(error => {
          alert(error)
        });
    

  }
  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        width: '90%',
        height: '90%'
      }}
    >
    <Card
    
    sx={{
      flexDirection: 'row',
      alignItems: 'center',
      justifyContent: 'center',
      width: '90%',
      height: '90%'
    }} 
    >
      <CardHeader title="Editar Parámetros del Algoritmo Genético"></CardHeader>
      <CardContent m={2} 
      sx={{
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'center',
        width: '90%',
        height: '90%',
        overflow: 'auto'
      }} 
      >
        <form onSubmit={handleSubmit}>
          <FormGroup sx={{ padding: 2, borderRadius: 2, border: '1px solid', borderColor: 'primary.main' }}>
            <Grid container spacing={2} p={2}>
              <Grid item xs={6}>
                <TextField
                  name='n_pop'
                  type="number"
                  InputProps={{ inputProps: { min: 1} }}
                  required
                  fullWidth
                  id="outlined-required"
                  label="Cantidad de Población Inicial"
                  value={parametersAI.n_pop}
                  InputLabelProps={{ shrink: true }} 
                  onChange={e => setParametersAI({
                    ...parametersAI,
                    n_pop: e.target.value
                  })}
                  error={!!errorPob}
                  helperText={errorPob}
                />
              </Grid>
              <Grid item xs={6}>
                <TextField
                  name='n_gen'
                  type="number"
                  InputProps={{ inputProps: { min: 1} }}
                  required
                  fullWidth
                  id="outlined-required"
                  label="Cantidad de Generaciones"
                  value={parametersAI.n_generations}
                  InputLabelProps={{ shrink: true }} 
                  onChange={e => setParametersAI({
                    ...parametersAI,
                    n_generations: e.target.value
                  })}
                  error={!!errorGen}
                  helperText={errorGen}
                />
              </Grid>
              <Grid item xs={6}>
                <TextField
                  name='p_selection'
                  type="number"
                  InputProps={{ inputProps: { min: 0, step: 0.01} }}
                  required
                  fullWidth
                  id="outlined-required"
                  label="% de Selección"
                  value={parametersAI.p_selection}
                  InputLabelProps={{ shrink: true }} 
                  onChange={e => setParametersAI({
                    ...parametersAI,
                    p_selection: e.target.value
                  })}
                  error={!!errorSelect}
                  helperText={errorSelect}
                />
              </Grid>
              <Grid item xs={6}>
                <TextField
                  name='p_mutate'
                  type="number"
                  step=".01"
                  InputProps={{ inputProps: { min: 0, step: 0.01} }}
                  required
                  fullWidth
                  id="outlined-required"
                  label="% de Mutación"
                  value={parametersAI.p_mutate}
                  InputLabelProps={{ shrink: true }} 
                  onChange={e => setParametersAI({
                    ...parametersAI,
                    p_mutate: e.target.value
                  })}                  
                  error={!!errorMutate}
                  helperText={errorMutate}
                />
              </Grid>
              
              <Grid item xs={6}>
                <TextField
                  name='min'
                  type="number"
                  InputProps={{ inputProps: { min: 0} }}
                  required
                  fullWidth
                  id="outlined-required"
                  label="Mínimo de producto"
                  InputLabelProps={{ shrink: true }} 
                  value={parametersAI.min_value}
                  onChange={e => setParametersAI({
                    ...parametersAI,
                    min_value: e.target.value
                  })}
                  error={!!errorMin}
                  helperText={errorMin}
                />
              </Grid>
              <Grid item xs={6}>
                <TextField
                  name='max'
                  type="number"
                  InputProps={{ inputProps: { min: 1} }}
                  required
                  fullWidth
                  id="outlined-required"
                  label="Máximo de producto"
                  InputLabelProps={{ shrink: true }} 
                  value={parametersAI.max_value}
                  onChange={e => setParametersAI({
                    ...parametersAI,
                    max_value: e.target.value
                  })}
                  
                  error={!!errorMax}
                  helperText={errorMax}
                />
              </Grid>

              <Grid item xs={6}>
                <TextField
                  name='max_weight'
                  type="number"
                  InputProps={{ inputProps: { min: 0} }}
                  required
                  fullWidth
                  id="outlined-required"
                  label="Máximo de peso permitido"
                  InputLabelProps={{ shrink: true }} 
                  value={parametersAI.max_weight}
                  onChange={e => setParametersAI({
                    ...parametersAI,
                    max_weight: e.target.value
                  })}
                  
                  error={!!errorMaxWeight}
                  helperText={errorMaxWeight}
                />
              </Grid>

              <Grid item xs={6}>
                <TextField
                  name='budget'
                  type="number"
                  InputProps={{ inputProps: { min: 0} }}
                  required
                  fullWidth
                  id="outlined-required"
                  label="Presupuesto"
                  InputLabelProps={{ shrink: true }} 
                  value={parametersAI.budget}
                  onChange={e => setParametersAI({
                    ...parametersAI,
                    budget: e.target.value
                  })}
                  
                  error={!!errorBudget}
                  helperText={errorBudget}
                />
              </Grid>


              <Grid item xs={11} >
              </Grid>
              <Grid item xs={1}>
                <Button type="submit" variant="outlined" disabled={disabled}>Enviar </Button>
              </Grid>
            </Grid>
          </FormGroup>
        </form>
      </CardContent>

    </Card>
    </Box>

  );


}

export default PutParameters;